/**
*
* - Scalar type -
*
* Scale: 1.250 / major third
* Base: 16px
* top level componenets set font size in rem
* borders in px
* everything else in em's
*
*/

@import url('https://fonts.googleapis.com/css?family=Roboto:300,400,500');

:root {
  font-family: 'Roboto', sans-serif;
  font-size: calc(.215vw + .215vh + 1vmin); // adds up to ~1em
  color: $color-text;
  font-weight: 300;
  line-height: 1.45;
  color: #333;
}

p,
article li {
  margin-bottom: 1.3em;
  // line-height: 1.5em;
}

a {
  text-decoration: none;
  color: $color-link;

  &:hover {
    text-decoration: underline;
    color: $color-primary;
  }
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 1.414em 0 0.5em;
  font-weight: 400;
  line-height: 1.2;
  color: $color-primary;
}

h4,
h5,
h6 {
  color: $color-primary;
}

h1,
h4 {
  margin-top: 0;
  font-size: 2.074em;
}

h2,
h5 {
  font-size: 1.728em;
}

h3,
h6 {
  font-size: 1.44em;
}

